@import '../../styles/mixins.scss';

.background {
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: row;

    .contentContainer {
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin: 0 auto;
        gap: 10px;
        width: 320px;

        .oauthBtn {
            @include flex-align-center;
            justify-content: center;
            height: 45px;
            gap: 8px;
            border-radius: 10px;
            border: 1px solid #EAEAEA;
            background-color: transparent;
            cursor: pointer;
            margin-top: 15px;

            img {
                width: 25px;
            }

            span {
                font-weight: 600;
                color: #4E4E4E;
                text-transform: none;
            }
        }

        .hrText {
            display: flex;
            align-items: center;
            margin: 15px 0;

            div {
                flex: 1;
                height: 1px;
                background-color: #D2D2D2;
            }

            span {
                padding: 0 10px;
                color: #a8a8a8;
                font-weight: 500;
            }
        }

        .logo {
            @include flex-align-center;
            gap: 15px;
            cursor: pointer;
            text-decoration: none;
            color: $base-dark-color;
            position: absolute;
            top: 40px;
            left: 10%;
    
            h2 {
                font-size: 24px;
                font-weight: 700;
            }
        }

        .heading {
            text-align: start;
    
            h1 {
                font-weight: 700;
                font-size: 36px;
                color: $base-dark-color;
                margin-bottom: 0px;
            }
    
            span {
                font-weight: 300;
                font-size: 18px;
                line-height: 20px;
                color: #9D9D9D;
            }
        }

        .inputs {
            .form {
                display: flex;
                flex-direction: column;
                margin-bottom: 10px;
    
                &:last-child {
                    margin-bottom: 25px;
                }
    
                label {
                    font-weight: 600;
                    font-size: 12px;
                    line-height: 15px;
                    color: #A0A0A0;
                    margin-bottom: 4px;
                }
    
                a {
                    text-align: right;
                    font-size: 14px;
                    line-height: 15px;
                    color: $base-dark-color;
                    align-self: flex-end;
                }
    
                .field {
                    position: relative;
                    height: 45px;
                    max-width: 320px;
    
                    input {
                        width: 100%;
                        height: 100%;
                        padding: 0 20px;
                        border-radius: 10px;
                        border: 1px solid #E0E0E0;
    
                        &:focus {
                            outline: 1px solid #c2c2c2;
                        }
                    }
                }
            }
        }
        .button {
            max-width: 340px;
            height: 50px;
            text-align: center;
    
            button {
                @include default-btn;
                width: 100%;
            }
            span {
                color: #A0A0A0;
                font-size: 14px;
                line-height: 15px;
            }
            a {
                font-size: 14px;
                line-height: 15px;
                color: $base-dark-color;
                margin-left: 4px;
            }
        }
    }

    .image {
        position: relative;
        width: 42%;
        height: calc(100% - 40px);
        margin: 20px;
    
        img {
            -webkit-user-drag: none;
            position: absolute;
            width: 100%;
            height: 100%;
            object-fit: cover;
            z-index: -10;
            border-radius: 60px;
            filter: brightness(70%);
        }
    
        .text {
            position: absolute;
            top: 50%;
            left: 10%;
            transform: translate(0%, -50%);
            color: #fff;
    
            h2 {
                font-weight: 800;
                font-size: 3em;
                line-height: 60px;
                margin: 0;
                text-transform: uppercase;
                max-width: 500px;
            }
        }
    }
}

@media screen and (max-width: 1368px) {
    .image {
        width: 55px;
    }
}

@media screen and (max-width: 900px) {
    .image {
        display: none;
    }
}