@import '../../styles/mixins.scss';

.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f8f8f8;
    
    h1 {
        font-size: 3rem;
        font-weight: bold;
        text-align: center;
        margin-bottom: 1rem;
    }

    p {
        font-size: 1.5rem;
        text-align: center;
        margin-bottom: 2rem;
    }

    .redirect_btn {
        @include default-btn;
    }
}