@import '../../styles/mixins.scss';

.container {
    @include flex-justify-between;
    flex-direction: column;
    width: 100%;
    min-width: 470px;
    height: 500px;
    background-color: $base-lightgray-color;
    border-radius: 50px;
    padding: 50px 45px;

    .inputs {
        display: flex;
        flex-direction: column;
        gap: 20px;

        .title {
            border: none;
            border-bottom: 1px solid #CECECE;
            outline: none;
            background-color: transparent;
            font-size: 26px;
            font-weight: 700;
            width: 100%;
        }

        .title::placeholder {
            font-size: 26px;
            font-weight: 700;
        }

        .money {
            display: flex;
            gap: 10px;
            width: 100%;

            .price {
                width: 100%;
            }

            .currency {
                min-width: 100px;
            }
        }
    }

    .buttons {
        .publishBtn {
            @include default-btn;
            width: 100%;
        }

    }
}

@media screen and (max-width: 500px) {
    .container {
        min-width: 200px;
        .bottom {
            .visitors {

                .avatars {
                    max-width: 300px;
                }
            }
        }
    }
}